import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { IonicStorageModule } from '@ionic/storage';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';
import { HttpModule } from '@angular/http';
import { FileTransfer } from '@ionic-native/file-transfer';
import { FileOpener } from '@ionic-native/file-opener';

import { MyApp } from './app.component';
import { TabsPage } from '../pages/tabs/tabs';


import { SDKBrowserModule } from '../shared/sdk/index';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { Camera } from '@ionic-native/camera';




@NgModule({
  declarations: [
    MyApp,
    TabsPage
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp, {}, 
{
  links: [
    { loadChildren: '../pages/home/home.module.ngfactory#AccountPageModuleNgFactory', name: 'HomePage', segment: 'home', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/kepwal/kepwal.module.ngfactory#KepwalPageModuleNgFactory', name: 'KepwalPage', segment: 'kepwal', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/kontak/kontak.module.ngfactory#KontakPageModuleNgFactory', name: 'KontakPage', segment: 'kontak', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/login/login.module.ngfactory#LoginPageModuleNgFactory', name: 'LoginPage', segment: 'login', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/perda/perda.module.ngfactory#PerdaPageModuleNgFactory', name: 'PerdaPage', segment: 'perda', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/perpus/perpus.module.ngfactory#PerpusPageModuleNgFactory', name: 'PerpusPage', segment: 'perpus', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/perwal/perwal.module.ngfactory#PerwalPageModuleNgFactory', name: 'PerwalPage', segment: 'perwal', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/profil/profil.module.ngfactory#ProfilPageModuleNgFactory', name: 'ProfilPage', segment: 'profil', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/tracking/dashboard-tracking/account-tracking/account-tracking.module.ngfactory#AccountTrackingPageModuleNgFactory', name: 'AccountTrackingPage', segment: 'account-tracking', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/tracking/dashboard-tracking/change-password/change-password.module.ngfactory#ChangePasswordPageModuleNgFactory', name: 'ChangePasswordPage', segment: 'change-password', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/tracking/dashboard-tracking/dashboard-tracking.module.ngfactory#DashboardTrackingPageModuleNgFactory', name: 'DashboardTrackingPage', segment: 'dashboard-tracking', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/tracking/dashboard-tracking/detail-tracking/detail-tracking.module.ngfactory#DetailTrackingPageModuleNgFactory', name: 'DetailTrackingPage', segment: 'detail-tracking', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/tracking/dashboard-tracking/edit-tracking/edit-tracking.module.ngfactory#EditTrackingPageModuleNgFactory', name: 'EditTrackingPage', segment: 'edit-tracking', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/tracking/dashboard-tracking/form-disposisi/form-disposisi.module.ngfactory#FormDisposisiPageModuleNgFactory', name: 'FormDisposisiPage', segment: 'form-disposisi', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/tracking/dashboard-tracking/profile-account-tracking/profile-account-tracking.module.ngfactory#ProfileAccountTrackingPageModuleNgFactory', name: 'ProfileAccountTrackingPage', segment: 'profile-account-tracking', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/tracking/people-detail-view/people-detail-view.module.ngfactory#PeopleDetailViewPageModuleNgFactory', name: 'PeopleDetailViewPage', segment: 'people-detail-view', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/tracking/tracking.module.ngfactory#TrackingPageModuleNgFactory', name: 'TrackingPage', segment: 'tracking', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/update/update.module.ngfactory#UpdatePageModuleNgFactory', name: 'UpdatePage', segment: 'update', priority: 'low', defaultHistory: [] }
  ]
}),
    SDKBrowserModule.forRoot(),
    IonicStorageModule.forRoot(),
    HttpModule,
    BrowserAnimationsModule
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    TabsPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    FileTransfer,
    Camera,
    FileOpener,
    {provide: ErrorHandler, useClass: IonicErrorHandler}
  ]
})
export class AppModule {}



